@import "../../styles/variables";

.transaction {
  &:nth-child(odd) li {
    background-color: #FBFBFB;
  }

  &:nth-child(even) li {
    background-color: #F1F1F1;
  }
  
  li {
    padding: 0.6rem 0 0.6rem 0.7rem;
    margin: 0;
    overflow: hidden;

    a {
      color: #3A80BA;
    }
  }
}